  .brand-detail {
    width: 100%;
    height: 125px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--white);
    border-radius: 10px;
    box-shadow: 0px 6px 25px #00000008;
    @media only screen and (max-width: 575px) {
      width: 100%;
      height: 100px;
    }
    @media only screen and (min-width: 1025px) and (max-width: 1299px) {
      width: 190px;
    }
    .brand-image{
      width: auto;
      height: 100px;
      object-fit: contain;
    }
  }
}
